'use strict';

import mongoose from 'mongoose';

var BusinessSchema = new mongoose.Schema({
  email: {
  	type:String
  },
  profession: {
  	type:String
  },
  description: {
  	type:String
  },
  address: {
  	type:Array
  },
  facebookUrl: {
  	type: String
  },
  instagramUrl: {
  	type:String
  },
  twitterUrl: {
  	type:String
  },
  officeLocation: {
  	type:Array
  },
  phoneNumber: {
  	type: Number
  },
  pinterestPage: {
  	type:String
  },
  storeName: {
  	type:String
  },
  mapAnnotation: {
  	type:Array
  },
  mapOriginLatitude: {
  	type:Number
  },
  mapOriginLongitude: {
  	type:Number
  },
  mapZoomLevel: {
  	type:Number
  },
  createdAt: {
    type: Date,
    default: Date.now
  },
  updatedAt: { 
    type: Date 
  }
});

export default mongoose.model('Business', BusinessSchema);
